import { Route, Redirect, Switch } from 'react-router-dom';
import React from 'react';
import { useDispatch } from 'react-redux';

import styles from './App.module.css';
import Header from '../components/Header';
import Shop from '../pages/Shop';
import Cart from '../pages/Cart';
import Footer from '../components/Footer';

import { fetchWeather } from '../store/actions/weather';

// http://localhost:3000/db.json
//const dbLink = 'http://localhost:3000/db.json';

// —Å—Å—ã–ª–∫–∞ –¥–ª—è –ê–ø–∏ —á–µ—Ä–µ–∑ –≤—ã–±—Ä–∞–Ω–Ω—ã–π –≥–æ—Ä–æ–¥ http://api.openweathermap.org/data/2.5/weather?q=${selectedCity},ru&appid=${weatherAPIKey}

// URL –∑–Ω–∞—á–∫–∞ –ø–æ–≥–æ–¥—ã `http://openweathermap.org/img/wn/${iconCode}@2x.png`

const App = () => {
	const dispatch = useDispatch();
	React.useEffect(() => {
		dispatch(fetchWeather());
	}, []);

	return (
		<div className={styles.App}>
			<Header />

			<div>
				<Switch>
					<Route path='/shop' component={Shop} />
					<Route path='/cart' component={Cart} />
					<Redirect from='/' to='/shop/all' />
				</Switch>
			</div>
			<Footer />
		</div>
	);
};

export default App;
