const initialState = {
	selectedSort: { name: '–ù–∞–∑–≤–∞–Ω–∏—é', type: 0 },
	selectedSize: undefined,
	categories: [
		{ name: '–í—Å–µ', type: 'ALL' },
		{ name: '–ö—É—Ä—Ç–∫–∏', type: 'JACKET' },
		{ name: '–®–∞–ø–∫–∏', type: 'HAT' },
		{ name: '–®–∞—Ä—Ñ—ã', type: 'SCARF' },
		{ name: '–°–≤–∏—Ç–µ—Ä—ã', type: 'SWEATER' },
	],
	sortBy: [
		{ name: '–ù–∞–∑–≤–∞–Ω–∏—é', type: 0 },
		{ name: '–¶–µ–Ω–µ', type: 1 },
		{ name: '–ü–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type: 2 },
	],
};

const filters = (state = initialState, action) => {
	switch (action.type) {
		case 'SET_SORT_BY':
			return {
				...state,
				selectedSort: action.payload,
			};

		case 'SET_CATEGORY':
			return {
				...state,
				selectedCategory: action.payload,
			};
		default:
			return state;
	}
};

export default filters;
