const initialState = {
	city: 'Nizhny Novgorod',
	iconID: 0,
	weather: {},
	isLoaded: false,

	suggestions: [
		{ t: 10, value: '–ù–∞ —É–ª–∏—Ü–µ –ø—Ä–æ—Ö–ª–∞–¥–Ω–æ, –Ω–µ –∑–∞–±—É–¥—å –∫—É—Ä—Ç–∫—É!' },
		{ t: 0, value: '–ù–∞ —É–ª–∏—Ü–µ –º–æ–∂–µ—Ç –±—ã—Ç—å —Å–ª—è–∫–æ—Ç—å. –ù–µ –ø—Ä–æ–º–æ–∫–Ω–∏!' },
		{ t: '-5', value: '–°–∞–º–æ–µ –≤—Ä–µ–º—è –¥–ª—è –∏–≥—Ä—ã –≤ —Å–Ω–µ–∂–∫–∏! –ù–µ –∑–∞–±—É–¥—å –ø–µ—Ä—á–∞—Ç–∫–∏!' },
		{ t: '-10', value: '–ó–∞ –æ–∫–Ω–æ–º –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ —Ö–æ–ª–æ–¥–Ω–æ! –¢–µ–ø–ª—ã–µ —à—Ç–∞–Ω—ã –∏ —à–∞—Ä—Ñ –Ω–µ –ø–æ–º–µ—à–∞—é—Ç!' },
		{ t: '-15', value: '–ë–µ—Ä–µ–≥–∏—Å—å –≥–æ–ª–æ–ª–µ–¥–∞ –∏ –æ–¥–µ–≤–∞–π—Å—è —Ç–µ–ø–ª–µ–µ!' },
		{ t: '-100', value: '–ù—É –∏ –º–æ—Ä–æ–∑ —Å–µ–≥–æ–¥–Ω—è...' },
	],
};

const weather = (state = initialState, action) => {
	switch (action.type) {
		case 'SET_WEATHER':
			return {
				...state,
				city: action.payload.city,
				iconID: action.payload.iconID,
				weather: action.payload.weather,
				isLoaded: true,
			};
		default:
			return state;
	}
};

export default weather;
